import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);
export default new Vuex.Store({
    state:{
        count:0,
        money:10
    },
    actions:{
       aa(ctx){
           ctx.commit("increment")
       }
    },
    mutations:{
        increment(state){
            state.count++
        }
    },
    getters:{
        num(state){
            return state.count+10
        }
    }
})



