<template>
  <div class="hello">
    <img src="../assets/1.jpg" alt>
    <p @click="bb">con</p>
    <p>{{count}}</p>
    <p>{{ax}}</p>
    <button @click="increment">点击</button>
    <p>{{num}}</p>
    <img src="../../static/5.png" alt="">
  </div>
</template>

<script>
import fun from "../assets/function.js";
import { mapState, mapMutations,mapGetters } from "vuex";
export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  computed: {
    ...mapState(["count"]),
    ...mapGetters(["num"]),
    ax(){
      return this.msg+"123"
    }
  },
  methods: {
    ...mapMutations(["increment"]),
    bb() {
      return fun.a();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.aa {
  width: 100px;
  height: 100px;
  background-color: red;
}
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
img{
  width:50px;
  height: 50px;
}
</style>



